<template>
  <div class="container position-relative">
    <b-overlay no-wrap :show="isLoading" rounded="sm" />
    <div v-if="moduleList.length>1" class="mb-3">
      <template v-for="(m,i) in moduleList">
        <button @click="activeModule = m.id" :key="i+'-modules'" class="btn font-weight-bold font-size-md mr-3 py-4 min-w-150px" :class="activeModule === m.id? 'btn-primary': 'btn-link-primary'">{{ m.label }}</button>
      </template>
    </div>
    <div class="row mb-3">
      <div class="col-5 pr-md-0">
        <div class="card card-custom card-stretch">
          <div class="card-body">
            <div class="row">
              <div class="form-group position-relative col-12 mb-5">
                <span class="d-block mb-2 font-weight-bolder" for="">Pilih Divisi / AP</span>
                <v-select :disabled="datasetList.division.disabled" :clearable="false" v-model="mainForm.division_id" :appendToBody="true" class="vs-style custom" :options="datasetList.division.data" :placeholder="'Pilih salah satu Divisi'">
                  <slot name="no-options">{{ $t('error.no_data') }}</slot>
                </v-select>
                <b-overlay no-wrap :show="activeModule && datasetList.division.disabled" :z-index="9" rounded="sm" :opacity=".95">
                  <template #overlay>
                    <div class="d-flex flex-fill align-items-center justify-content-center font-weight-bold min-w-350px">
                      <i class="ri-lock-line ri-2x font-weight-bold text-danger"></i>
                      <span class="flex-fill pl-2">Tidak ada Divisi / AP di {{ moduleList.filter(x => x.id === activeModule)[0].label }} </span>
                    </div>
                  </template>
                </b-overlay>
              </div>
              <div class="form-group col-12 mb-0">
                <span class="d-block mb-2 font-weight-bolder" for="">Tahun</span>
                <date-picker :clearable="false" :input-attr="{ autocomplete:'off' }" v-model="mainForm.periode_year" type="year" :input-class="'form-control custom-dateyears'" :value-type="formatDate('year')" :formatter="momentFormat" />
              </div>
            </div>
          </div>
        </div>
      </div>
      <div class="col">
        <div class="card card-custom card-stretch">
          <div class="card-body">
            <div class="row no-gutters align-items-center">
              <div class="col p-0">
                <div class="form-group mb-5">
                  <span class="d-block mb-2 font-weight-bolder" for="">Pilih Agenda Input</span>
                  <v-select :disabled="false" :clearable="false" v-model="mainForm.agenda_id" :appendToBody="true" class="vs-style custom" :options="datasetList.agenda.data" :placeholder="'Pilih salah satu Agenda'">
                    <slot name="no-options">{{ $t('error.no_data') }}</slot>
                  </v-select>
                </div>
                <div class="form-group position-relative mb-0">
                  <span class="d-block mb-1 font-weight-bolder" for="">Pilih Skenario Input</span>
                  <div class="checkbox-inline py-2">
                    <label v-for="(f,i) in datasetList.scenario.data" :key="i+'-form-a'" class="checkbox checkbox-lg checkbox-outline mb-1">
                      <input :disabled="globalFilter.agenda_id === 0" v-model="mainForm.scenario_input" :value="f" type="radio">
                      <span></span>{{ f.label }}</label>
                  </div>
                  <b-overlay no-wrap :show="globalFilter.agenda_id === 0" :z-index="9" rounded="sm" :opacity=".95">
                    <template #overlay>
                      <div class="d-flex flex-fill align-items-center justify-content-center font-weight-bold min-w-350px">
                        <i class="ri-lock-line ri-2x font-weight-bold text-danger"></i>
                        <span class="flex-fill pl-2">Tidak ada Skenario Input di RKAP</span>
                      </div>
                    </template>
                  </b-overlay>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
    <div :class="fullscreen? 'fullscreen-component': ''" class="position-relative">
      <div class="card card-custom">
        <div class="card-header border-0 p-5" style="min-height:50px">
          <div class="d-flex flex-fill flex-wrap align-items-center justify-content-between">
            <div class="flex-fill">
              <h3 class="card-title font-weight-bolder text-dark ">{{ config.title }} <template v-if="moduleList.length>1 && activeModule"> - {{ moduleList.filter(x => x.id === activeModule)[0].label }}</template></h3>
            </div>
            <div class="d-flex flex-wrap align-items-center">
              <a @click="fullscreen = !fullscreen" class="btn btn-link text-primary mr-1">
                <i :class="fullscreen? 'ri-fullscreen-exit-line' : 'ri-fullscreen-line'" class="text-primary"></i>
                <span>Fullscreen</span>
              </a>
              <!-- <button @click="$refs.inputFile.click()" class="btn btn-primary font-size-sm font-weight-bolder"><i class="ri-upload-2-line"></i> Upload File </button> -->
              <!-- <input ref="inputFile" type="file" hidden @change="uploadImport($event)" accept="application/vnd.openxmlformats-officedocument.spreadsheetml.sheet, application/vnd.ms-excel"> -->
            </div>
          </div>
        </div>
        <div class="card-body px-5 pt-0 pb-0">
          <b-tabs :content-class="mainData.length>1? 'mt-3' : 'mt-0'" :nav-wrapper-class="mainData.length>1? '' : 'd-none'" active-nav-item-class="active-nav-title font-weight-bolder text-primary">
            <b-tab v-for="(d,i) in mainData" :key="i+'-table-data'" :title="d.label" active>
              <div v-if="d.data.length" class="table-responsive sticky-table">
                <table class="table table-head-custom table-bordered table-vertical-cente table-input">
                  <capex-node v-for="(x,y) in d.data" :row="x" :key="y+'-loop-data'" />
                </table>
              </div>
            </b-tab>
          </b-tabs>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import moment from 'moment'
moment.locale('id')
export default {
  name: 'proses-capex',
  components: {
    CapexNode: () => import('./_components/capex-node.vue')
  },
  data () {
    return {
      isLoading: false,
      fullscreen: false,
      config: {
        title: 'Proses CAPEX',
        permission: {
          read: 'template-allow-all'
        }
      },
      momentFormat: {
        stringify: (date) => { return date ? moment(date).format('YYYY') : '' },
        parse: (value) => { return value ? moment(value, 'YYYY').toDate() : null }
      },
      searchFilter: '',
      globalFilter: {},
      mainData: [],
      mainForm: {},
      moduleList: [
        {
          id: 'PC',
          label: 'Proses CAPEX',
          export: null,
          import: null,
          queue: null,
          permission: null,
          dataset: [
            {
              // eslint-disable-next-line object-property-newline
              id: 'pcdt', endpoint: 'custom/process-capex', label: '', type: 'component', pk: 'project_name',
              fields: [
                { data: 'item_name', label: 'Uraian', isMoney: false, class: 'sl', action: true }
              ],
              // eslint-disable-next-line object-property-newline
              monthly: { data: 'data_series_months', header: 'periode_month', parse: 'longMonth', pipe: 'number', isMoney: true, isPercent: false }, total: true, subtotal: true
            }
          ]
        }
      ],
      moduleDataset: {
        PC: { division: { disabled: false, api: 'divisions' } }
      },
      activeModule: null,
      datasetList: {
        division: { allowNull: 'Semua Divisi', disabled: true, api: null, param: { sort: 'ASC', sort_by: 'division_id' }, option: { label: 'division_name', code: 'id' }, data: [], target: 'division_id' },
        agenda: { disabled: false, api: null, param: null, option: null, data: [], target: null },
        scenario: { disabled: false, api: null, param: null, option: null, data: [{ code: 'pesimist', label: 'Skenario Pesimis' }, { code: 'moderate', label: 'Skenario Moderat' }, { code: 'optimist', label: 'Skenario Optimis' }], target: null }
      },
      isUploading: false,
      uploadMax: 1,
      uploadLimitExceed: false,
      uploadError: null,
      uploadPercentage: 0,
      tableConfig: [],
      queueKey: 0
    }
  },
  watch: {
    mainForm: {
      deep: true,
      immediate: false,
      handler (e) {
        if (e) {
          let newFilter = Object.assign({}, this.globalFilter, {
            agenda_id: e.agenda_id.code,
            division_id: e.division_id.code,
            periode_year: moment(moment(e.periode_year, 'YYYY').toDate()).format('YYYY-MM-DD'),
            scenario_input: e.scenario_input.code
          })
          if (this.datasetList.division.disabled) delete newFilter.division_id
          if (JSON.stringify(newFilter) !== JSON.stringify(this.globalFilter)) {
            this.globalFilter = Object.assign({}, newFilter)
          }
        }
      }
    },
    activeModule: {
      deep: false,
      immediate: true,
      handler (e) {
        if (this.moduleDataset[e]) {
          this.$set(this.datasetList, 'division', { ...this.datasetList.division, ...this.moduleDataset[e].division })
          this.getDataset()
          this.queueKey++
        }
      }
    },
    globalFilter: {
      deep: true,
      immediate: false,
      handler (e) {
        if (this.activeModule) {
          if (this.moduleDataset[this.activeModule].division.disabled) {
            this.getMainData(e)
          } else {
            if (e.division_id) this.getMainData(e)
            else {
              if (this.datasetList.division.allowNull) this.getMainData(e)
            }
          }
        } else console.log('no active Modules', this.activeModule)
      }
    }
  },
  async mounted () {
    this.$_sys.togleClass('kt_body', 'aside-minimize', true)
    this.$store.commit('set', ['sideNavSecondary', false])
    await this.getDataset('PC')
  },
  methods: {
    staticDataset () {
      let result = []
      for (let i = 0; i < 13; i++) {
        const e = -Math.abs(i)
        result.push({ label: i === 0 ? 'RKAP' : 'Stress Test ' + moment().set('date', 1).set('month', i - 1).format('MMMM'), code: i === 0 ? i : e })
      }
      this.$set(this.datasetList.agenda, 'data', result)
      this.$set(this.mainForm, 'agenda_id', result[0])
      this.$set(this.mainForm, 'scenario_input', this.datasetList.scenario.data[1])
    },
    async getDataset (setActive = null) {
      if (setActive) this.activeModule = setActive
      this.mainForm = Object.assign({}, {
        division_id: { label: null, code: null },
        periode_year: moment().set('date', 1).set('month', 0).format('YYYY'),
        agenda_id: { label: null, code: null },
        scenario_input: { label: null, code: null }
      })
      for (let i = 0; i < Object.keys(this.datasetList).length; i++) {
        const e = Object.keys(this.datasetList)[i]
        if (this.datasetList[e].api) {
          this.$_api.dataset(this.datasetList[e].api, this.datasetList[e].param).then(res => {
            let result = res.data.map(x => { return { label: x[this.datasetList[e].option.label], code: x[this.datasetList[e].option.code], data: x } })
            if (this.datasetList[e].allowNull) result.unshift({ label: this.datasetList[e].allowNull, code: null })
            this.$set(this.datasetList[e], 'data', result)
            if (this.datasetList[e].target && result.length) this.$set(this.mainForm, this.datasetList[e].target, result[0])
          }, err => {
            this.$_alert.error(err)
          })
        } else this.$set(this.mainForm, this.datasetList[e].target, { label: null, code: null })
      }
      await this.staticDataset()
    },
    downloadImport () {
      this.isLoading = true
      const selected = this.moduleList.filter(x => x.id === this.activeModule)[0]
      let xlsTitle = this.mainForm.division_id.data.division_code
      xlsTitle += '_' + selected.id
      xlsTitle += this.globalFilter.agenda_id === 0 ? '_RKAP' : '_ST-' + moment().set('date', 1).set('month', (Math.abs(this.globalFilter.agenda_id) - 1)).format('MMM') + '_' + this.mainForm.scenario_input.code
      xlsTitle += '_' + moment(this.globalFilter.periode_year).format('YYYY')
      this.$_api.downloadFile(selected.export, this.globalFilter, xlsTitle + '.xlsx').then(res => {
        this.isLoading = false
      }, err => {
        this.isLoading = false
        this.$_alert.error(err)
      })
    },
    formatDate (type) {
      let _format
      switch (type) {
        case 'date':
        case 'date-range':
          _format = 'YYYY-MM-DD'
          break
        case 'datetime':
        case 'datetime-range':
          _format = 'YYYY-MM-DD HH:mm:ss'
          break
        case 'year':
        case 'year-range':
          _format = 'YYYY'
          break
        case 'month':
        case 'month-range':
          _format = 'YYYY-MM-DD'
          break
        case 'time':
        case 'time-range':
          _format = 'HH:mm:ss'
          break
        case 'week':
        case 'week-range':
          _format = 'DD'
          break
      }
      return _format
    },
    async getMainData (e) {
      this.mainData = []
      this.isLoading = true
      let form = Object.assign({}, e)
      form.scenario_input = form.agenda_id === 0 ? 'rkap' : form.scenario_input
      const activeModule = this.moduleList.filter(x => x.id === this.activeModule)[0]
      if (activeModule.dataset.length > 1) {
        for (let i = 0; i < activeModule.dataset.length; i++) {
          const el = activeModule.dataset[i]
          await this.$_api.dataset(el.endpoint, form).then(res => {
            res.label = el.label
            res.config = el
            this.mainData.push(res)
          }, err => {
            this.$_alert.error(err)
          })
        } this.isLoading = false
      } else {
        this.$_api.dataset(activeModule.dataset[0].endpoint, form).then(res => {
          res.label = activeModule.dataset[0].label
          res.config = activeModule.dataset[0]
          this.mainData.push(res)
          this.isLoading = false
        }, err => {
          this.isLoading = false
          this.$_alert.error(err)
        })
      }
    }
  }
}
</script>

<style>
  .nav-tabs .nav-link.active.active-nav-title {
    background-color: #fff !important;
  }
</style>
<style scoped>
  .timeline.timeline-6:before {
    left: 1.5px !important;
  }
  .form-control.custom-dateyears {
    height: calc(1.5em + 1.19rem) !important;
  }
  .fullscreen-component {
    position: absolute !important;
    padding: 0 !important;
    top: 0;
    left: 0;
    width: 100vw !important;
    height: 100vh !important;
    z-index: 99999 !important;
    overflow-y: scroll;
  }
  .fullscreen-component .card {
    min-height: 100%;
  }
  .sticky-table {
    max-height: 90vh;
  }
  .sticky-table {
    position: sticky;
    top: -1px;
  }
  .sticky-table table thead tr {
    position: sticky;
    top: -1px;
    z-index: 3;
  }
  .table-input .sr {
    position: sticky;
    right: 1px; /* border */
  }
  .table-input .sl {
    position: sticky;
    left: -1px; /* border */
  }
  .table-input .sl div {
    width: 350px !important;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
    overflow: hidden;
    text-overflow: ellipsis;
  }
  .table-input th.sl,
  .table-input th.sr {
    background: var(--info-light);
  }
  .table-input td.sl,
  .table-input td.sr {
    background: #fff;
  }
  .table-input td {
    padding: 3px 10px;
  }
  .download-div {
    min-width: 270px;
    max-width: 270px;
    border: 2px solid #ebedf3;
    border-radius: 8px;
  }
  .popover-xl {
    z-index: 999999 !important;
    max-width: 700px !important;
    width: 700px !important;
  }
</style>



